/**
 * @constructor
 * Properties supported are $coltype, $colsubtype, $colsublen, $ident, $name
 * @param list
 * @param colNumber
 */
function omnis_list_col(list, colNumber)
{
	this.mList = list;
	this.mColNumber = colNumber;
}

// Function to get a property of a column
omnis_list_col.prototype.getProperty = function(propNumber)
{
	if (this.mColNumber > this.mList.lstDef.length)
		return null;
	switch (propNumber)
	{
		case eListProperties.objtype:
		case eListProperties.coltype:			return this.mList.lstDef[this.mColNumber - 1][1];
		
		case eListProperties.objsubtype:
		case eListProperties.colsubtype:	return this.mList.lstDef[this.mColNumber - 1][2];
		
		case eListProperties.objsublen:
		case eListProperties.colsublen:		return this.mList.lstDef[this.mColNumber - 1][3];
		
		case eBaseProperties.ident:				return this.mColNumber;
		case eBaseProperties.name:				return this.mList.lstDef[this.mColNumber - 1][0];
		// Start rmm8482
		case eBaseProperties.count:				return this.$count(false); 
		case eListProperties.total:				return this.mList.total(this.mColNumber - 1, false);
		case eListProperties.minimum:			return this.mList.minimum(this.mColNumber - 1, false);
		case eListProperties.maximum:			return this.mList.maximum(this.mColNumber - 1, false);
		case eListProperties.average:			return this.mList.average(this.mColNumber - 1, false);
		// End rmm8482
	}
	return null;
};

// Start rmm8482: Certain properties have arguments (selected only), so they can also be requested here
omnis_list_col.prototype.callMethod = function(propName, selonly)
{
	switch (propName)
	{
		case "$count":		return this.$count(selonly);
		case "$total":		return this.mList.total(this.mColNumber - 1, selonly); 
		case "$minimum":	return this.mList.minimum(this.mColNumber - 1, selonly);
		case "$maximum":	return this.mList.maximum(this.mColNumber - 1, selonly);
		case "$average":	return this.mList.average(this.mColNumber - 1, selonly);
	}
	return null;
};
// End rmm8482

// Function to get $canassign for a property of a column
omnis_list_col.prototype.getCanAssign = function(propNumber)
{
	return propNumber != eBaseProperties.ident;
};

// Function to set a property of a list column
omnis_list_col.prototype.setPropCli = function(propNumber, propValue)
{
	if (this.mColNumber > this.mList.lstDef.length)
		return false;
	switch (propNumber)
	{
		case eListProperties.coltype:			
			propValue *= 1;
			if (propValue == 0)
				return false;
			this.mList.lstDef[this.mColNumber - 1][1] = propValue; 
			break;
		case eListProperties.colsubtype:	
			propValue *= 1;
			if (propValue == 0)
				return false;
			this.mList.lstDef[this.mColNumber - 1][2] = propValue; 
			break;
		case eListProperties.colsublen:		
			propValue *= 1;
			this.mList.lstDef[this.mColNumber - 1][3] = propValue; 
			break;
		case eBaseProperties.name:
			if (typeof propValue != "string" || propValue.length == 0)
				return false;
			var col = this.mList.resolveColumn(propValue);
			if (col && (col + 1) != this.mColNumber)
				return false;
			this.mList.lstDef[this.mColNumber - 1][0] = propValue;
			break;
		default:
			return false;
	}
	this.mList.markAsModified();
	return true;
};

// rmm9361: Renamed and reworked to handle both remove and select duplicates
omnis_list_col.prototype.remselduplicates = function(sortnow, ignorecase, remove)
{
	if (this.lstRow)
		return 0;

	sortnow = jOmnis.defaultParameter(sortnow, false);
	ignorecase = jOmnis.defaultParameter(ignorecase, false);

	var theList = this.mList;
	// Sort if required
	if (sortnow)
	{
		var col = "C" + this.mColNumber;
		if (ignorecase)
		{
			theList.$sort(function ($ref)
			{
				return jOmnis.fn.upp(jOmnis.getValue($ref, col)); 
			}, false);
		}
		else
		{
			theList.$sort(function ($ref)
			{
				return jOmnis.getValue($ref, col); 
			}, false);
		}
	}

	var selstatechanged = false;
	function selline(n, issel)
	{
		if (issel != theList.lstLSEL[n - 1])
		{
			selstatechanged = true;
			theList.lstLSEL[n - 1] = issel;
		}
	}

	// Process rows to check for duplicate values in this column	
	var colIndex = this.mColNumber - 1;
	var lineCount = theList.lstData.length;
	var colValue = theList.lstData[lineCount - 1][colIndex];
	if (ignorecase && typeof colValue == "string")
		colValue = colValue.toUpperCase();

	var count = 0;
	theList.checkWrapper();
	if (!remove)
		selline(lineCount, 0);
	for (var n = lineCount - 1; n >= 1; n--)
	{
		if (!remove)
			selline(n, 0);
		var colValue2 = theList.lstData[n - 1][colIndex];
		if (ignorecase && typeof colValue == "string")
			colValue2 = colValue2.toUpperCase();
		if (colValue2 != colValue)
		{
			colValue = colValue2;
		}
		else
		{
			count++;
			if (remove)
				theList.__list.deleteRows(n + 1);
			else
				selline(n + 1, 1);
		}	
	}
	if (selstatechanged || (remove && count))
		theList.markAsModified();
	return count;
};

// Start rmm9361
omnis_list_col.prototype.$removeduplicates = function(sortnow, ignorecase)
{
	return this.remselduplicates(sortnow, ignorecase, true);
}

omnis_list_col.prototype.$selectduplicates = function(sortnow, ignorecase)
{
	return this.remselduplicates(sortnow, ignorecase, false);
}
// End rmm9361

// Column $clear
omnis_list_col.prototype.$clear = function()
{
	if (this.mColNumber > this.mList.lstDef.length)
		return false;
	this.mList.initColumn(this.mColNumber);
	// Successful return value
	return true;
};

// Column $count
omnis_list_col.prototype.$count = function(selonly)
{
	selonly = jOmnis.defaultParameter(selonly, false);
	if (this.mColNumber > this.mList.lstDef.length)
		return 0;
	var count = 0;
	var colIndex = this.mColNumber - 1;
	if (this.mList.lstRow)
		return (!selonly && this.mList.lstData[colIndex] != null) ? 1 : 0;
	for (var i = 0; i < this.mList.lstData.length; ++i)
	{
		if (!selonly || this.mList.islinesel(i + 1))
		{
			if (this.mList.lstData[i][colIndex] != null)
				++count;
		}
	}
	return count;
};

// ###################################################################
// ##### Object representing a list row ##############################
// ###################################################################
/**
 * @constructor
 * Properties supported are $rownumber and $selected
 * @param list
 * @param rowNumber
 */
function omnis_list_row(list, rowNumber)
{
	this.mList = list;
	this.mRowNumber = rowNumber;
}

// Function to get a column from a list row
omnis_list_row.prototype.getValue = function() // rmm7746
{
	return jListNotation(this, arguments, arguments.length); // rmm7746
};

// Function to set a column in a list row
omnis_list_row.prototype.setValue = function() // rmm7746
{
	// Start rmm7746
	this.mSingleColumn = 0;	// rmm11584: jListNotation sets this to the number of the single updated column in a top-level list row, if that is all it changes
	if (arguments.length >= 2 && jListNotation(this, arguments, arguments.length - 1, arguments[arguments.length - 1]))
	{
		// Start rmm11584: If a single column was updated in a top-level list row, just update that, otherwise a data grid completely re-renders
		if (this.mSingleColumn) {
			var form = this.mList.__form;
			if (!form.disableAutomaticUpdates) {
				var dataIndex = this.mList.__dataIndex;
				var xn0 = dataIndex % 65536;
				var xn1 = Math.floor(dataIndex / 65536);
				form.generateAutomaticUpdate(xn0 - 1, xn1 - 1, "#1COL", this.mRowNumber, this.mSingleColumn);
			}
		}
		else
			// End rmm11584
			this.mList.markAsModified();
		return true;
	}
	return false;
	// End rmm7746
};

// Start rmm8300
omnis_list_row.prototype.xaddValue = function ()
{
	var cellValue = jListNotation(this, arguments, arguments.length - 1);
	cellValue += arguments[arguments.length - 1];
	if (jListNotation(this, arguments, arguments.length - 1, cellValue))
		this.mList.markAsModified();
	return jListNotation(this, arguments, arguments.length - 1);
};

omnis_list_row.prototype.xsubValue = function ()
{
	var cellValue = jListNotation(this, arguments, arguments.length - 1);
	cellValue -= arguments[arguments.length - 1];
	if (jListNotation(this, arguments, arguments.length - 1, cellValue))
		this.mList.markAsModified();
	return jListNotation(this, arguments, arguments.length - 1);
};

omnis_list_row.prototype.xmulValue = function ()
{
	var cellValue = jListNotation(this, arguments, arguments.length - 1);
	cellValue *= arguments[arguments.length - 1];
	if (jListNotation(this, arguments, arguments.length - 1, cellValue))
		this.mList.markAsModified();
	return jListNotation(this, arguments, arguments.length - 1);
};

omnis_list_row.prototype.xdivValue = function ()
{
	var cellValue = jListNotation(this, arguments, arguments.length - 1);
	cellValue /= arguments[arguments.length - 1];
	if (jListNotation(this, arguments, arguments.length - 1, cellValue))
		this.mList.markAsModified();
	return jListNotation(this, arguments, arguments.length - 1);
};
// End rmm8300

// Start rmm7746
// Assign pAssignValue to the row if possible
// Returns true if a value was assigned.
omnis_list_row.prototype.assign = function(pAssignValue)
{
	if (pAssignValue == null)
		return false;

	var srcRowArray;
	if (pAssignValue instanceof omnis_raw_list)
	{
		if (pAssignValue.lstRow)
			srcRowArray = pAssignValue.lstData;
		else if (pAssignValue.lstL >= 1 && pAssignValue.lstL <= pAssignValue.lstData.length)
			srcRowArray = pAssignValue.lstData[pAssignValue.lstL - 1];
	}
	else if (pAssignValue instanceof omnis_list_row)
	{
		if (pAssignValue.mRowNumber >= 1 && pAssignValue.mRowNumber <= pAssignValue.mList.lstData.length)
			srcRowArray = pAssignValue.mList.lstData[pAssignValue.mRowNumber - 1];
	}
	if (srcRowArray)
	{
		this.mList.checkWrapper();
		this.mList.copyArgumentsToRow.apply(this.mList, [this.mRowNumber].concat(srcRowArray));
		return true;
	}
	return false;
};
// End rmm7746

// Function to get a property of a row
omnis_list_row.prototype.getProperty = function(propNumber)
{
	switch (propNumber)
	{
		case eListProperties.line:	// Alias for row number
		case eListProperties.rownumber:
			return this.mRowNumber;
		case eListProperties.selected:
			return this.mList.islinesel(this.mRowNumber);
	}
	return null;
};

// Function to get $canassign for a property of a row
omnis_list_row.prototype.getCanAssign = function(propNumber)
{
	return eListProperties.selected == propNumber;
};

// Function to set a property of a list row
omnis_list_row.prototype.setPropCli = function(propNumber, propValue)
{
	switch (propNumber)
	{
		case eListProperties.selected:
		{
			if (this.mList.lstLSEL && this.mRowNumber >= 1 && this.mRowNumber <= this.mList.lstLSEL.length)
			{
				this.mList.lstLSEL[this.mRowNumber - 1] = (propValue*1 != 0) ? 1 : 0;
				this.mList.markAsModified();
				return true;
			}
			return false;
		}
	}
	return false;
};


omnis_list_row.prototype.$assignrow = function (sourceRow, byName) {
	// Start jmg1210 (replaces rmm11398 - didn't mark list as modified)
	const origLstL = this.tempSetListLine();
	const result = this.mList.$assignrow(sourceRow, byName);
	this.mList.lstL = origLstL;
	return result;
	// End jmg1210
};

// Start jmg1210
omnis_list_row.prototype.$clear = function() {
	const cols = this.mList.$cols();
	this.mList.actingOnRow = this.mRowNumber;

	let col = cols.$first();
	let result = true;
	while (col && result) {
		result = col.$clear();
		col = cols.$next(col);
	}

	delete this.mList.actingOnRow;
	return	result;
}

omnis_list_row.prototype.$assigncols = function() {
	const origLstL = this.tempSetListLine();
	const result = this.mList.$assigncols.apply(this.mList, arguments);
	this.mList.lstL = origLstL;
	return result;
}

omnis_list_row.prototype.$loadcols = function() {
	const origLstL = this.tempSetListLine();
	this.mList.$loadcols.apply(this.mList, arguments);
	this.mList.lstL = origLstL;
}

/**
 * Temporarily change the row's associated list's $line to that to which this row pertains.
 * @return {Number}		The original list $line. Should be re-applied after the row notation completes.
 */
omnis_list_row.prototype.tempSetListLine = function() {
	const origLstL = this.mList.lstL;
	this.mList.lstL = this.mRowNumber;
	return origLstL;
}
// End jmg1210

// ###################################################################
// ##### Object representing list columns ($cols()) ##################
// ###################################################################
/**
 * @constructor
 * @param list
 */
function omnis_cols(list)
{
	this.mList = list;
}

// Function to get an object in the group by name
omnis_cols.prototype.getValue = function(name)
{
	var col = this.mList.resolveColumn(name);
	if (col == null)
		return null;
	return new omnis_list_col(this.mList, col + 1);
};

// Function to get a property of an object
// name is the name of the object in the group
omnis_cols.prototype.getProperty = function(name, propNumber)
{
	var obj = this.getValue(name);
	if (obj)
		return obj.getProperty(propNumber);
	return null;
};

// Function to get $canassign for a property of an object
// name is the name of the object in the group
omnis_cols.prototype.getCanAssign = function(name, propNumber)
{
	var obj = this.getValue(name);
	if (obj)
		return obj.getCanAssign(propNumber);
	return false;
};

// Function to set a property of an object
// name is the name of the object in the group
omnis_cols.prototype.setPropCli = function(name, propNumber, propValue)
{
	var obj = this.getValue(name);
	if (obj)
		return obj.setPropCli(propNumber, propValue);
	return false;
};

// Columns $count()
omnis_cols.prototype.$count = function()
{
	return this.mList.lstDef.length;
};

// Add a column ($add, $addafter, $addbefore)

// Parameters are: either fieldname or name,type,subtype,len
// In the case of fieldname, the variable must be an instance variable
// rmm7844: $cols.$add,$addafter and $addbefore now have an initial array parameter to allow us to get to the name of a variable
omnis_cols.prototype.$add = function(array,name,type,subtype,len)
{
	// Start rmm7855: Detect internal calls - first argument is a string, so the definition is actually just
	// function(name,type,subtype,len)
	var rdefEntry;
	if (typeof array === "string")
		rdefEntry = this.createRdefEntry(arguments[0], arguments[1], arguments[2], arguments[3]);
	else
		rdefEntry = this.createRdefEntry(name, type, subtype, len, array[0]);
	// End rmm7855
	if (!rdefEntry)
		return null;
	// Start rmm8122
	if (!this.mList.lstDef)
		this.mList.lstDef = [];
	// End rmm8122
	this.mList.lstDef.push(rdefEntry);
	this.mList.initColumn(this.mList.lstDef.length);
	return new omnis_list_col(this.mList, this.mList.lstDef.length);
};

omnis_cols.prototype.$addafter = function(array,aftercol,name,type,subtype,len)
{
	var newCol;
	var afterColumn = (Object.prototype.toString.call(array) === '[object Array]') ? aftercol : array; //jmg0058
	if (afterColumn && afterColumn instanceof omnis_list_col)
	{
		if (afterColumn.mList != this.mList)
			return null;
		newCol = afterColumn.mColNumber + 1;
	}
	else if (jOmnis.isInteger(afterColumn))
	{
		newCol = afterColumn*1 + 1;
	}
	if (newCol == null)
		return null;

	// Start jmg0058
	var rdefEntry;
	if (afterColumn == array) // jmg0060
		rdefEntry = this.createRdefEntry(arguments[1], arguments[2], arguments[3], arguments[4]);
	else
		rdefEntry = this.createRdefEntry(name, type, subtype, len, array[1]);
	// End jmg0058

	if (rdefEntry)
		return this.insertColumn(newCol, rdefEntry);
	return null;
};

omnis_cols.prototype.$addbefore = function(array,beforecol,name,type,subtype,len)
{
	var newCol;
	var beforeColumn = (Object.prototype.toString.call(array) === '[object Array]') ? beforecol : array; //jmg0058
	if (beforeColumn && beforeColumn instanceof omnis_list_col)
	{
		if (beforeColumn.mList != this.mList)
			return null;
		newCol = beforeColumn.mColNumber;
	}
	else if (jOmnis.isInteger(beforeColumn))
	{
		newCol = beforeColumn*1;
	}
	if (newCol == null)
		return null;

	// Start jmg0058
	if (beforeColumn == array) // jmg0060
		var rdefEntry = this.createRdefEntry(arguments[1], arguments[2], arguments[3], arguments[4]);
	else
		var rdefEntry = this.createRdefEntry(name, type, subtype, len, array[1]);
	// End jmg0058

	if (rdefEntry)
		return this.insertColumn(newCol, rdefEntry);
	return null;
};

// Create RDEF entry from parameter information
omnis_cols.prototype.createRdefEntry = function (name, type, subtype, len, variableName) // rmm7844: Added variableName
{
	// Start rmm8122: Special values need to be handled for kList and kRow
	if (129 == type)
		type = eOmnisDataTypes.fftList;
	else if (229 == type)
		type = eOmnisDataTypes.fftRow;
	else if (type == eOmnisDataTypes.fftDate && subtype >= 1000) // jmg0702
		subtype = eOmnisDataSubTypes.dpFdtimedefault + 8 * (subtype - 1000); // jmg0702

	// End rmm8122
	var col = this.mList.resolveColumn((type == null && variableName != null) ? variableName : name);	// rmm7844
	if (col != null)
		return null;	// Column already exists

	if (type == null)
	{
		// Just using a name
		var dataIndex = jOmnis.currentForm.getDataIndex(variableName != null ? variableName : name, null, false);	// rmm7844: now use variableName if present
		if (dataIndex != 0)
		{
			return [].concat(jOmnis.currentForm.getDefn(dataIndex));
		}
		type = eOmnisDataTypes.fftCharacter;
		subtype = 0;
		len = gCliMaxcharlen;
	}
		
	return [name, type, subtype, len, 0, 0];
};

// Insert a column into the associated list
omnis_cols.prototype.insertColumn = function(column, rdefEntry)
{
	this.mList.lstDef.splice(column - 1, 0, rdefEntry);
	this.mList.insertColumn(column);
	this.mList.initColumn(column);
	return new omnis_list_col(this.mList, column);
};

// Columns $remove()
omnis_cols.prototype.$remove = function(col)
{
	var colToRemove;
	if (col instanceof omnis_list_col && col.mList == this.mList)
		colToRemove = col.mColNumber;
	else if (jOmnis.isInteger(col))
		colToRemove = col*1;
	
	if (colToRemove == null || colToRemove < 1 || colToRemove > this.mList.lstDef.length)
		return false;
	
	colToRemove -= 1;
	this.mList.lstDef.splice(colToRemove, 1);
	if (this.mList.lstRow)
	{
		this.mList.lstData.splice(colToRemove, 1);
	}
	else
	{
		for (var i = 0; i < this.mList.lstData.length; ++i)
		{
			this.mList.lstData[i].splice(colToRemove, 1);
		}
	}
	this.mList.markAsModified();
};

// Columns $appendlist()
omnis_cols.prototype.$appendlist = function(list, getrowfunc)
{
	if (!omnis_raw_list.isRawList(list, false))
		return false;
	var colObj = new omnis_list_col(this.mList, 1);
	for (var i = 1; i <= this.mList.lstDef.length; ++i)
	{
		colObj.mColNumber = i;
		var row = getrowfunc(colObj);
		
		list.lstData.push(list.convertTypes(row));
		list.lstLSEL.push(0);
	}
	list.markAsModified();
	// Successful return value
	return true;
};

// Columns $insertlist()
omnis_cols.prototype.$insertlist = function(list, line, getrowfunc)
{
	if (!omnis_raw_list.isRawList(list, false))
		return false;
	list.checkWrapper();	// Need omnis_list methods
	var colObj = new omnis_list_col(this.mList, 1);
	for (var i = 1; i <= this.mList.lstDef.length; ++i)
	{
		colObj.mColNumber = i;
		var row = getrowfunc(colObj);
		
		var rowNumber = list.__list.addRow(line + i - 1, list.__list.getColumnCount());
		list.lstData[rowNumber - 1] = list.convertTypes(row);
	}
	list.markAsModified();
	// Successful return value
	return true;
};

// Columns $makelist()
omnis_cols.prototype.$makelist = function(getrowfunc)
{
	var retList = null;
	var colObj = new omnis_list_col(this.mList, 1);
	for (var i = 1; i <= this.mList.lstDef.length; ++i)
	{
		colObj.mColNumber = i;
		var row = getrowfunc(colObj);
		
		if (i == 1)
		{
			retList = omnis_raw_list.defineMakelistResult(row);
		}
		retList.lstData.push(row);
		retList.lstLSEL.push(0);
	}
	return retList;
};

// Columns $findident()
omnis_cols.prototype.$findident = function(ident)
{
	// Start rmm7438
	if (!jOmnis.isInteger(ident))
		return null;
	ident *= 1;
	if (ident >= 1 && ident <= this.mList.lstDef.length)
		return new omnis_list_col(this.mList, ident);
	// End rmm7438
	return null;
};

// Columns $findname()
omnis_cols.prototype.$findname = function(name)
{
	return this.getValue(name);
};

// Columns $first()
omnis_cols.prototype.$first = function()
{
	if (this.mList.lstDef.length > 0)
		return new omnis_list_col(this.mList, 1);
	return null;
};

// Columns $next()
omnis_cols.prototype.$next = function(currentCol)
{
	if (currentCol && currentCol instanceof omnis_list_col && currentCol.mList == this.mList)
	{
		if (currentCol.mColNumber < this.mList.lstDef.length)
			return new omnis_list_col(this.mList, currentCol.mColNumber + 1);
	}
	return null;
};

// Columns $sendall()
// Passed two arguments - the first is a function containing the action to perform for each object, the second (optional)
// determines if the object is included in the objects to be processed.
omnis_cols.prototype.$sendall = function(actionFunc, filterFunc)
{
	if (actionFunc == null)
		return false;
	var colObj = new omnis_list_col(this.mList, 1);
	for (var i = 1; i <= this.mList.lstDef.length; ++i)
	{
		colObj.mColNumber = i;
		if (filterFunc == null || filterFunc(colObj))
			actionFunc(colObj);
	}
	return true;
};

// ###################################################################
// ###### Raw list containing methods for client method execution ####
// ###################################################################
/**
 * @constructor
 * @param list {boolean|omnis_raw_list|Object}
 * 			Pass true/false to create a new empty Row/List respectively.
 * 			Pass an existing omnis_raw_list to create a COPY.
 * 			Pass a list-LIKE object (containing lstDef, lstData members etc) to wrap that data in an omnis_raw_list (i.e. don't take a copy unless forceCopy is true).
 * @param [form] {omn_form}
 * @param [obj] {jOmnisRemoteObject}
 * @param [forceCopy] {boolean} If true, and a list-like object is passed, a copy will be created.
 */
function omnis_raw_list(list, form, obj, forceCopy) // rmm9444 // bjw0703
{
	if (typeof list === "boolean")
	{
		// Constructing a new list
		this.lstRow = list ? 1 : 0;
		this.lstDef = [];
		this.lstData = [];
		if (!this.lstRow)
		{
			this.lstLSEL = [];
			this.lstL = 0;
		}
		// When called like this, the form is also supplied (to be a context for list methods)
		// Start rmm9444
		this.__form = form;
		this.__obj = obj;
		// End rmm9444
	}
	else if (list instanceof omnis_raw_list) // jmg0719: If the passed list is an existing omnis_raw_list, create a COPY.
		return jOmnis.copyValue(list); // jmg0719

	else // 'list' is a list-like object (containing lstData, lstDef members etc) - do not create a copy - we are effectively WRAPPING the existing data in an omnis_raw_list.
	{
		// Start bjw0703: Wrap the data in an omnis_raw_list and return a copy
		if (forceCopy) {
			return jOmnis.copyValue(new omnis_raw_list(list));
		}
		// End bjw0703
		// Constructing for an existing list-like object
		this.lstRow = list.lstRow;
		this.lstDef = list.lstDef;
		this.lstData = list.lstData;
		if (!this.lstRow)
		{
			this.lstLSEL = list.lstLSEL;
			this.lstL = list.lstL;
		}	
	}
}

// Start jmg1246
omnis_raw_list.prototype.toString = function() {
	return JSON.stringify(this, function (k, v) {
		// Ignore members with names starting __ (internal members of list/date objects etc)
		if (k.length > 2 && k.startsWith("__"))
			return undefined;
		return v;
	});
};
// End jmg1246

// Method to get selection state of a list line as quickly as possible
omnis_raw_list.prototype.islinesel = function(line)
{
	if (this.lstLSEL && line >= 1 && line <= this.lstLSEL.length)
		return this.lstLSEL[line - 1] ? true : false;
	return false;
};

// Method to set selection state as quickly as possible
omnis_raw_list.prototype.selline = function(line, issel)
{
	if (this.lstLSEL)
		return this.lstLSEL[line - 1] = issel;
};

// Resolve column number parameter passed to a list function - return value is zero-based column number
omnis_raw_list.prototype.resolveColumn = function(col)
{
	this.checkWrapper();
	if (!col)
		return null;
	if (jOmnis.isInteger(col))
		return col*1 - 1;
	var colNumber = this.__list.findColumn(col);
	if (!colNumber)
		return null;
	return colNumber - 1;
};

// Note for getValue/setValue - if there is a single qualifier, and the object is an omnis list rather than an omnis row, then
// the qualifier refers to a list line if it is numeric, or a column in the current line of the list if it is non-numeric

// Get a row or a column value.  The parameters of getValue correspond to the list notation list.row.col, or row.col for example
omnis_raw_list.prototype.getValue = function() // rmm7746
{
	return jListNotation(this, arguments, arguments.length);
};

// Set a row or a column value.  The parameters of setValue correspond to the list notation list.row.col, or row.col for example
// The last parameter is the value to which the row or column value is to be set.
// The first component of the notation corresponds to this (the omnis_raw_list), the remaining components of the notation are the parameter values up to the last
// parameter.
// If the 2nd parameter is an array, just a single cell, pointed to by the array [row1,col1,row2,col2...], will be set (to the value in the last parameter) (jmg0048)
omnis_raw_list.prototype.setValue = function() // rmm7746
{
	// Start jmg0048
	// If the 2nd argument is an array, treat as setting an individual (nested) list cell:
	if (Object.prototype.toString.call(arguments[1]) === '[object Array]')
	{
		this.__form.setData(this.__dataIndex, arguments[arguments.length - 1], "#NCELL", arguments[1], null, true, "a");
	}
	// End jmg0048

	// Start rmm7746
	// Special case for setting a column in a row - we just want to mark the column as modified to reduce traffic sent to the server
	if (this.lstRow && arguments.length == 2)
	{
		var colIndex = this.resolveColumn(arguments[0]);
		if (colIndex != null)
		{
			var value = this.convertDataType(arguments[1], colIndex);
			this.lstData[colIndex] = jOmnis.copyValue(value);
			this.markAsModified(colIndex);
			return true;
		}
	}
	else if (arguments.length >= 2 && jListNotation(this, arguments, arguments.length - 1, arguments[arguments.length - 1]))
	{
		this.markAsModified();
		return true;
	}
	return false;
	// End rmm7746
};

// Start rmm8300
omnis_raw_list.prototype.xaddValue = function ()
{
	var cellValue = jListNotation(this, arguments, arguments.length - 1);
	cellValue += arguments[arguments.length - 1];
	if (jListNotation(this, arguments, arguments.length - 1, cellValue))
		this.markAsModified();
	return jListNotation(this, arguments, arguments.length - 1);
};

omnis_raw_list.prototype.xsubValue = function ()
{
	var cellValue = jListNotation(this, arguments, arguments.length - 1);
	cellValue -= arguments[arguments.length - 1];
	if (jListNotation(this, arguments, arguments.length - 1, cellValue))
		this.markAsModified();
	return jListNotation(this, arguments, arguments.length - 1);
};

omnis_raw_list.prototype.xmulValue = function ()
{
	var cellValue = jListNotation(this, arguments, arguments.length - 1);
	cellValue *= arguments[arguments.length - 1];
	if (jListNotation(this, arguments, arguments.length - 1, cellValue))
		this.markAsModified();
	return jListNotation(this, arguments, arguments.length - 1);
};

omnis_raw_list.prototype.xdivValue = function ()
{
	var cellValue = jListNotation(this, arguments, arguments.length - 1);
	cellValue /= arguments[arguments.length - 1];
	if (jListNotation(this, arguments, arguments.length - 1, cellValue))
		this.markAsModified();
	return jListNotation(this, arguments, arguments.length - 1);
};
// End rmm8300

// Get the $selected value of a row of a list
omnis_raw_list.prototype.getSelected = function(row)
{
	if (jOmnis.isInteger(row))
	{
		var rowIndex = row*1 - 1;
		if (this.lstLSEL && rowIndex >= 0 && rowIndex < this.lstLSEL.length)
			return this.lstLSEL[rowIndex] ? true : false;
		// Treat other values as not selected (the data array may have expanded)
		return false;
	}
	return null;
};

// Set the $selected value of a row of a list
omnis_raw_list.prototype.setSelected = function(row, isSelected)
{
	if (jOmnis.isInteger(row))
	{
		var rowIndex = row*1 - 1;
		if (this.lstLSEL && rowIndex >= 0 && rowIndex < this.lstData.length)	// Check lstData length, to allow us to expand the selection array
		{
			var oldSelected = this.lstLSEL[rowIndex] ? true : false;
			var newSelected = isSelected ? true : false;
			if (oldSelected != newSelected)
			{
				this.lstLSEL[rowIndex] = newSelected ? 1 : 0;
				this.markAsModified();
			}
			// Return value of $assign
			return true;
		}
	}
	return false;
};

// Function to get a property of a list
// Properties supported are $colcount, $line, $linecount, $linemax
omnis_raw_list.prototype.getProperty = function(propNumber)
{
	switch (propNumber)
	{
		case eListProperties.linecount:	return this.lstRow ? 1 : this.lstData.length;
		case eListProperties.linemax:		return this.lstRow ? 1 : null;	// linemax does not apply when executing on the client
		case eListProperties.line:			return this.lstRow ? null : this.lstL;
		case eListProperties.colcount:	return this.lstDef ? this.lstDef.length : 0;
	}
	return null;
};

// Function to get $canassign for a property of a list
omnis_raw_list.prototype.getCanAssign = function(propNumber)
{
	return propNumber != eListProperties.linemax && propNumber != eListProperties.colcount && !this.lstRow;
};

// Function to set a property of a list
omnis_raw_list.prototype.setPropCli = function(propNumber, propValue)
{
	// Start jmg0217: Allow for assigning to nested lists: (Assumes an even number of params, as extra params assumed to be row-col pairs)
	if (arguments.length > 2)
	{
		var theList = this;
		for (var n = 0; n < arguments.length - 2; n += 2) {
				theList = theList.getValue(arguments[n], arguments[n+1]);
		}

		// Start jmg0217a
		// The last 2 args will be the property number & value to assign:
		var success = theList.setPropCli(arguments[arguments.length - 2], arguments[arguments.length - 1]);
		if (success)
			this.markAsModified(); // Mark the top-level list as modified.

		return success; // End jmg0217a
	} // End jmg0217

	if (!jOmnis.isInteger(propValue))
		return false;	// All list properties to be set are integers
	propValue *= 1;	// Converts string to number
	switch (propNumber)
	{
		case eListProperties.linecount:
		{
			if (propValue < 0 || this.lstRow) return false;
			if (propValue < this.lstData.length)
			{
				this.lstData.splice(propValue, this.lstData.length - propValue);
			}
			else if (propValue > this.lstData.length)
			{
				var emptyRow = [];
				for (var j = 0; j < this.lstDef.length; ++j)
				{
					if (this.lstDef[j][1] == eOmnisDataTypes.fftInteger || this.lstDef[j][1] == eOmnisDataTypes.fftNumber || this.lstDef[j][1] == eOmnisDataTypes.fftSequence) // rmm7438
						emptyRow.push(0);
					else
						emptyRow.push(null);
				}
				for (var i = propValue - this.lstData.length; i > 0; --i)
				{
					this.lstData.push([].concat(emptyRow));
				}
			}
			// Check the selection too
			if (propValue < this.lstLSEL.length)
			{
				this.lstLSEL.splice(propValue, this.lstLSEL.length - propValue);
			}
			else if (propValue > this.lstLSEL.length)
			{
				for (var i = propValue - this.lstLSEL.length; i > 0; --i)
				{
					this.lstLSEL.push(0);
				}
			}
			// Make sure line is OK
			if (this.lstL > propValue)
				this.lstL = 0;	
			this.markAsModified();
			return true;
		}
		case eListProperties.line:			
		{
			if (propValue >= 0 && propValue <= (this.lstData.length + 1) && !this.lstRow) // rmm7679: allow linecount + 1 (like the core)
			{
				if (this.lstL != propValue)
				{
					this.lstL = propValue;
					this.markAsModified(null, "#L"); // rmm9505
				}
				return true;					
			}
			return false;
		}
	}
	return false;
};

// Parameter 1 of $define is an array of column names. The remaining columns are the variables from which the list is to be defined.
// Also used to process $redefine when this.mRedefine is true
omnis_raw_list.prototype.$define = function()
{
	if (!this.mRedefine)
	{
		this.lstDef = [];
		this.lstData = [];
		// rmm7509: Moved to here from below (noticed this issue when looking at the syntax error)
		if (!this.lstRow)
		{
			this.lstL = 0;
			this.lstLSEL = [];
		}
	}
	if (arguments.length <= 1)
		return;	// No columns
	var columnNames = arguments[0];
	for (var i = 1; i < arguments.length; ++i)
	{
		// Check to see if variable is an instance variable
		var varName = columnNames[i - 1];
		// Start rmm9444
		var done = false;
		if (this.__obj)
		{
			var defn = this.__obj.get_ivar_defn(varName);
			if (defn)
			{
				done = true;
				this.lstDef[i - 1] = defn;
			}
		}
		if (!done)
		{
			var dataIndex = this.__obj ? 0 : this.__form.getDataIndex(varName, null, false);
			// End rmm9444
			if (dataIndex != 0)
			{
				// The variable is an instance variable
				// Need to call a new method, form.getDefn to get the column definition from the data index
				this.lstDef[i - 1] = this.__form.getDefn(dataIndex);
			}
			else
			{
				// The variable should be a local or parameter variable - we use a type based on its contents
				this.lstDef[i - 1] = omnis_raw_list.createRdefEntry(varName, arguments[i], i);
			}
		}
		// If the list being defined is a row, initialise its data
		if (this.lstRow && !this.mRedefine)
		{
			this.lstData[i - 1] = "";	// This may need changing at some point, but just set to empty for now
		}
	}
	// If necessary, tell the form an instance variable has changed
	this.markAsModified();
	return true;
};

// List $redefine
// Parameters are passed in the same way as $define()
omnis_raw_list.prototype.$redefine = function()
{
	this.mRedefine = true;
	this.$define.apply(this, Array.prototype.slice.call(arguments, 0));
	delete this.mRedefine;	// rmm7843
	return true;
};

// Start rmm7608: List $definefromsqlclass
// Parameters are in groups of 4: name, type, subtype, sublen
omnis_raw_list.prototype.$definefromsqlclass = function()
{
	this.lstDef = [];
	this.lstData = [];
	if (!this.lstRow)
	{
		this.lstL = 0;
		this.lstLSEL = [];
	}
	var columns = arguments.length / 4;
	for (var col = 0; col < columns; ++col)
	{
		var argBase = col*4;
		this.lstDef.push([arguments[argBase], arguments[argBase + 1], arguments[argBase + 2], arguments[argBase + 3], 0, 0]);
	}
	// Start rmm10394: For a row, add initial data to each column
	if (this.lstRow)
	{
		for (var col = 0; col < columns; ++col)
		{
			var value;
			switch (this.lstDef[col][1])
			{
				case eOmnisDataTypes.fftCharacter:
					value = "";
					break;
				case eOmnisDataTypes.fftInteger:
				case eOmnisDataTypes.fftSequence:
				case eOmnisDataTypes.fftNumber:
					value = 0;
					break;
				case eOmnisDataTypes.fftBoolean:
					value = false;
					break;
				default: // jmg1123
					value = null; // jmg1123
			}
			this.lstData[col] = value;
		}
	}
	// End rmm10394
	// If necessary, tell the form an instance variable has changed
	this.markAsModified();
	return true;
};
// End rmm7608

// List $copydefinition
omnis_raw_list.prototype.$copydefinition = function(list)
{
	// Copy the definition from list
	if (!list || !(list instanceof omnis_raw_list))
		return false;
	// This method clears the data first
	this.$clear();
	this.lstDef = jOmnis.deepCopy(list.lstDef); // jmg0448
	// Start rmm11397: See $define - there is similar logic just before it completes its processing
	if (this.lstRow && this.lstDef.length > 0)
		this.lstData[this.lstDef.length - 1] = "";
	// End rmm11397
	return true;
};

// rmm8038: List $addcols
// Parameters are in groups of 4: name, type, subtype, sublen
omnis_raw_list.prototype.$addcols = function()
{
	var columns = parseInt(arguments.length / 4); // jmg0705
	if (columns * 4 !== arguments.length)
		this.throwListException("omn_list_badaddcols");	// rmm9444
	if (!this.lstDef)
		this.lstDef = [];
	var $cols = this.$cols();
	for (var col = 0; col < columns; ++col)
	{
		var argBase = col * 4;
		$cols.$add(arguments[argBase] + "", arguments[argBase + 1], arguments[argBase + 2], arguments[argBase + 3]);	// Force column name to be a string (see $cols.$add)
	}
};

// List $first
// Implementation ported from anumFirst in dmlstn.cpp
omnis_raw_list.prototype.$first = function(selonly, backwards, compareFunc) // rmm9356a: Added compareFunc
{
	// Need to use correct default values for parameters (kFalse, kFalse)
	// Return omnis_list_row object
	selonly = jOmnis.defaultParameter(selonly, false);
	backwards = jOmnis.defaultParameter(backwards, false);
	
	if (this.lstRow)
		return null;
	
	var retVal = null;
	var cnt = this.lstData.length;
	if (cnt > 0) 
	{
		var rowno = this.nextMatchingRow(backwards ? cnt : 1, selonly, backwards, compareFunc); // rmm9356a
		if (rowno > 0) 
		{
			this.lstL = rowno;
			retVal = new omnis_list_row(this, rowno);
		}
		else
		{
			this.lstL = 0;
		}
		this.markAsModified();
	}
	return retVal;
};

// List $next
// Implementation ported from anumNext in dmlstn.cpp
omnis_raw_list.prototype.$next = function(currentRow, selonly, backwards, compareFunc) // rmm9356a: Added compareFunc
{
	// Need to use correct default values for last two parameters (kFalse, kFalse)
	// currentRow can be omnis_list_row or a row number
	// Return omnis_list_row object
	selonly = jOmnis.defaultParameter(selonly, false);
	backwards = jOmnis.defaultParameter(backwards, false);
	
	if (this.lstRow)
		return null;
	
	var retVal = null;
	var cnt = this.lstData.length;
	if (cnt > 0) 
	{
		var rowno = 0;
		if (currentRow && currentRow instanceof omnis_list_row)
		{
			if (currentRow.mList != this)
				return null;
			rowno = currentRow.mRowNumber;
		}
		else if (jOmnis.isInteger(currentRow))
		{
			rowno = currentRow*1;
		}
		if (!rowno) rowno = this.lstL;
		
		if (backwards) { rowno--; if (rowno<0) rowno = cnt; }
		else rowno++; 
		if (rowno > 0 && rowno <= cnt)
			rowno = this.nextMatchingRow(rowno, selonly, backwards, compareFunc);
		// Start rmm10116
		else
			rowno = 0;
		// End rmm10116
		if (rowno > 0) 
		{
			this.lstL = rowno;
			retVal = new omnis_list_row(this, rowno);
		}
		else
		{
			this.lstL = 0;
		}
		this.markAsModified();
	}
	return retVal;
};

// rmm9356a: Made this a method
omnis_raw_list.prototype.nextMatchingRow = function (rowno, selonly, backwards, compareFunc)
{
	var lineObj, oldCurLine;
	if (compareFunc)
	{
		lineObj = new omnis_list_row(this, 1);
		oldCurLine = this.lstL;
	}
	var cnt = this.lstData.length;
	if (selonly || compareFunc) while (rowno > 0)
	{
		var matched = true;
		if (selonly && !this.islinesel(rowno))
			matched = false;
		if (matched && compareFunc)
		{
			lineObj.mRowNumber = rowno;
			this.lstL = rowno;	// Set to allow references like list.col
			matched = (compareFunc(lineObj) != 0);
			this.lstL = oldCurLine;
		}
		if (matched)
			break;
		if (backwards) rowno--; else { rowno++; if (rowno > cnt) rowno = 0; }
	}
	return rowno;
}

// rmm11680: List $count
omnis_raw_list.prototype.$count = function (selonly) {
	if (this.lstRow)
		return 0;
	this.checkWrapper();
	if (!selonly)
		return this.lstData.length;
	var count = 0;
	for (var i = 0; i < this.lstData.length; ++i) {
		if (this.lstLSEL[i])
			++count;
	}
	return count;
};

// List $add
omnis_raw_list.prototype.$add = function()
{
	if (this.lstRow)
		return null;
	this.checkWrapper();
	var rowNumber = this.__list.addRow(0, this.__list.getColumnCount());
	return this.copyArgumentsToRow.apply(this, [rowNumber].concat(Array.prototype.slice.call(arguments, 0)));
};

// List $addafter
omnis_raw_list.prototype.$addafter = function()
{
	if (this.lstRow)
		return null;
	this.checkWrapper();
	var rowNumber = this.__list.addRow(arguments[0] + 1, this.__list.getColumnCount());
	return this.copyArgumentsToRow.apply(this, [rowNumber].concat(Array.prototype.slice.call(arguments, 1)));
};

// List $addbefore
omnis_raw_list.prototype.$addbefore = function()
{
	if (this.lstRow)
		return null;
	this.checkWrapper();
	var rowNumber = this.__list.addRow(arguments[0], this.__list.getColumnCount());
	return this.copyArgumentsToRow.apply(this, [rowNumber].concat(Array.prototype.slice.call(arguments, 1)));
};

omnis_raw_list.prototype.copyArgumentsToRow = function(rowNumber)
{
	if (rowNumber != INVALID_LIST_OPERATION)
	{
		var colCount = this.__list.getColumnCount();
		var argsToCopy = arguments.length - 1;
		if (argsToCopy > colCount) argsToCopy = colCount;
		for (var i = 1; i <= argsToCopy; ++i) {
			this.lstData[rowNumber - 1][i - 1] = jOmnis.copyValue(arguments[i]);

			if (arguments[i] instanceof omnis_raw_list) //jmg0049
				this.lstData[rowNumber - 1][i - 1].__lstParent = this; //jmg0049 Add a __lstParent property to the nested list - ref to parent list.
		}
			
		// Convert to correct data types
		this.convertTypes(this.lstData[rowNumber - 1]);
		
		// Start rmm8372: If the number of arguments is less than the number of columns, set default values for the un-set columns
		if (argsToCopy < colCount)
		{
			for (var i = argsToCopy; i < colCount; ++i)
			{
				var rdefEntry = this.lstDef[i];
				if (rdefEntry)
				{
					var value;
					switch (rdefEntry[1])
					{
						case eOmnisDataTypes.fftCharacter: 
							value = ""; 
							break;
						case eOmnisDataTypes.fftInteger: 
						case eOmnisDataTypes.fftSequence:
						case eOmnisDataTypes.fftNumber:
							value = 0; 
							break;
						case eOmnisDataTypes.fftBoolean:
							value = false;
							break;
					}
					this.lstData[rowNumber - 1][i] = value;
				}
			}
		}
		// End rmm8372

		// If necessary, tell the form an instance variable has changed
		this.markAsModified();
		
		return new omnis_list_row(this, rowNumber);
	}
	return null;
};

// List $remove
omnis_raw_list.prototype.$remove = function(removeInfo)
{
	// removeInfo is either an omnis_list_row, a row number, kListKeepSelected or kListDeleteSelected
	var rowToRemove;
	if (removeInfo instanceof omnis_list_row && removeInfo.mList == this)
		rowToRemove = removeInfo.mRowNumber;
	else if (jOmnis.isInteger(removeInfo))
		rowToRemove = removeInfo*1;
		
	this.checkWrapper();
	switch (rowToRemove)
	{
		case kListDeleteSelected:	
			this.__list.deleteRows(eListDeleteRow.Selected);
			this.markAsModified();	// rmm9361
			return true;
		case kListKeepSelected:		
			this.__list.deleteRows(eListDeleteRow.Deselected); 
			this.markAsModified();	// rmm9361
			return true;
	}
	// A row number
	if (rowToRemove >= 1 && rowToRemove <= this.lstData.length)
	{
		this.__list.deleteRows(rowToRemove); 
		this.markAsModified();	// rmm9361
		return true;
	}
	return false;
};

// List $clear
omnis_raw_list.prototype.$clear = function()
{
	this.lstData = [];
	if (!this.lstRow)
	{
		this.lstLSEL = [];
		this.lstL = 0;
	}
	// If necessary, tell the form an instance variable has changed
	this.markAsModified();
};

// List $merge
// Implementation ported from anumMerge in dmlstn.cpp
omnis_raw_list.prototype.$merge = function(list, byname, selonly, cleardestlist)
{
	// cleardestlist defaults to kFalse
	cleardestlist = jOmnis.defaultParameter(cleardestlist, false);
  if (this.lstRow)
		return false;
	var rowno1 = 0;
	if (!omnis_raw_list.isRawList(list))
	{
		if (list instanceof omnis_list_row)
		{
			rowno1 = list.mRowNumber;
			list = list.mList;
		}
	}
	if (cleardestlist) this.lstData = [];
	
	this.checkWrapper();
	list.checkWrapper();
	
	var cnt = this.__list.getColumnCount(), cnt1 = list.__list.getColumnCount();
	var cols = [];
	{
		var name1;
		for (var col1 = 1; col1 <= cnt1; col1++) 
		{
			var col = 0;
			if (byname) 
				col = this.__list.findColumn(list.lstDef[col1 - 1][0]);
			else 
				col = col1;
			if (col <= 0 || col > cnt) col=0;
			cols[col1] = col;
		}
	}
	
	var min = 1, max = list.__list.getRowCount(); 
	if (rowno1 > 0) { min = rowno1; max = (rowno1 <= max ? rowno1 : 0); }
	for (var line1 = min; line1 <= max; line1++) 
	{
		if (!selonly || list.islinesel(line1))
		{
			var line = this.__list.addRow(0, cnt);		
			for (var col1 = 1; col1 <= cnt1; col1++) 
			{
				var col = cols[col1];
				if (col) 
				{ 
					var colVal;
					if (list.lstRow) colVal = list.lstData[col1 - 1];
					else colVal = list.lstData[line1 - 1][col1 - 1];
					this.lstData[line - 1][col - 1] = jOmnis.copyValue(colVal);
				}
			}
		}
	}
	this.markAsModified();
	return true;
};

// Check to see if we already have a wrapper object (omnis_list)
// omnis_list is a wrapper for omnis_raw_list and other objects which have the list members
omnis_raw_list.prototype.checkWrapper = function()
{
	if (this.__list == null)
		this.__list = new omnis_list(this);
}

// Mark the list variable as modified, if it is an instance variable
omnis_raw_list.prototype.markAsModified = function(colIndex, what, rows, cols)
{
	if ("__dataIndex" in this)
	{
		let newData;
		// Start jmg0049
		if (rows instanceof Array) { // Setting a nested cell/list
			var cell = this;
			// set newData to the cell referenced by 'rows':
			for (var i=0; i< rows.length - 2; i+=2) {
				cell = cell.lstData[rows[i] - 1][rows[i+1] - 1];
			}
			newData = cell.lstData[rows[i] - 1][rows[i+1] - 1];
		}
		else {
			// Start dmw0613
			switch (what) {
				case "#L":
					newData = this.lstL;
					break;
				case "#LSEL":
					newData = this.getSelected(rows); // rows should be a row number
					break;
				default:
					newData = this;
			}
			// End dmw0613
		}

		// colIndex may not be null when modifying a column in a row variable
		// If colIndex is not null, and data index is for a top level instance variable, just mark the column as modified
		var dataIndex = this.__dataIndex;
		if (colIndex != null)
		{
			var xn1 = Math.floor(dataIndex / 65536);
			if (xn1 == 0)
			{
				var xn0 = dataIndex % 65536;
				dataIndex = (colIndex + 1)*65536 + xn0;
				newData = this.lstData[colIndex];
			}
		}
		this.__form.setData(dataIndex, newData, what, rows, cols, true, "a");
	}
	else if ("__lstParent" in this) { // Start jmg0049 Check for a parent list
		// Find this nested list's position in its parent list:
		var parentData = this.__lstParent.lstData;
		mainloop:
		for (var r=0; r < parentData.length; r++) {
			for (var c=0; c < parentData[r].length; c++) {
				if (parentData[r][c] == this) {
					if (!rows || !(rows instanceof Array))
						rows = [];
					rows.push(r+1,c+1); //+1 as need to be 1-indexed
					break mainloop;
				}
			}
		}
		// Call into this function again, this time with the parent list, and the position to this list's cell defined:
		this.__lstParent.markAsModified(colIndex, "#NCELL", rows, cols);
	} // End jmg0049
};

// Static method to create rdef entry based on type of data
omnis_raw_list.createRdefEntry = function(colName, data, combinedXn)
{
	var rdefEntry;
	if (data != null && typeof data == "number")
	{
		if (jOmnis.isInteger(data))
			rdefEntry = [ colName, eOmnisDataTypes.fftInteger, 0, 0, combinedXn, 0 ];
		else
			rdefEntry = [ colName, eOmnisDataTypes.fftNumber, gCliDpFloat, 0, combinedXn, 0 ];
	}
	else if (data != null && typeof data == "boolean")
		rdefEntry = [ colName, eOmnisDataTypes.fftBoolean, 0, 0, combinedXn, 0 ];
	else if (data && data instanceof omnis_date)
		rdefEntry = [ colName, eOmnisDataTypes.fftDate, 0, 0, combinedXn, 0 ]; // Use zero for dp for now
	else if (data && data instanceof omnis_raw_list)
		rdefEntry = [ colName, eOmnisDataTypes.fftList, data.lstRow*1, 0, combinedXn, 0 ];
	else
		rdefEntry = [ colName, eOmnisDataTypes.fftCharacter, 0, gCliMaxcharlen, combinedXn, 0 ]; // Use zero for dp for now
	return rdefEntry;
};

// Static method to define the result of $makelist from a result row
omnis_raw_list.defineMakelistResult = function(row)
{
	var retList = new omnis_raw_list(false, jOmnis.currentForm);
	// Add columns, based on the data types of the returned row
	for (var j = 1; j <= row.length; ++j)
	{
		retList.lstDef.push(omnis_raw_list.createRdefEntry("Col" + j, row[j - 1], j));
	}
	return retList;
};

// Method to convert the types of the columns in a row to the types of the definition (used for $add,$appendlist/$insertlist, 
// so only deals with primitive types and dates).
omnis_raw_list.prototype.convertTypes = function(row)
{
	for (var i = 0; i < row.length; ++i)
	{
		var rdefEntry = this.lstDef[i];
		if (rdefEntry)
		{
			var value = row[i];
			if (value != null)
			{
				row[i] = jOmnis.convertDataType(value, rdefEntry[1], rdefEntry[2], rdefEntry[3], "Row column " + rdefEntry[0]); // rmm7623 // rmm7859
			}
		}
	}
	return row;	
};

// Method to convert data to be assigned to a list column to the correct type
// colIndex is a zero-relative column number
omnis_raw_list.prototype.convertDataType = function(data, colIndex)
{
	if (this.lstDef && colIndex < this.lstDef.length)
	{
		var defn = this.lstDef[colIndex];
		return jOmnis.convertDataType(data, defn[1], defn[2], defn[3], "column " + defn[0]); // rmm7623 // rmm7859
	}
	return data;
};

// Static method to check that a parameter is a raw list, and either a row or not a row
omnis_raw_list.isRawList = function(list, isRow)
{
	if (!list || !(list instanceof omnis_raw_list))
		return false;
	if (isRow == null)
		return true;
	return (!isRow && !list.lstRow) || (isRow && list.lstRow);
};

/**
 * Static function to attempt to get a raw row (omnis_raw_list with lstRow == 1) from:
 * 	1) An omnis_list_row
 * 	2) An omnis_raw_list of type row (lstRow == 1)
 * 	3) An omnis_raw_list, using the current line to determine the row
 * @param {omnis_list_row|omnis_raw_list} listOrRow
 * @returns {omnis_raw_list}
 */ // jmg0719
omnis_raw_list.ParseRow = function(listOrRow)
{
	var newRow;

	if (listOrRow instanceof omnis_list_row) {
		newRow = new omnis_raw_list(true, listOrRow.mList.__form, listOrRow.mList.__obj);
		newRow.lstDef = listOrRow.mList.lstDef;
		newRow.lstData = listOrRow.mList.lstData[listOrRow.mRowNumber-1];
	}
	else if (listOrRow instanceof omnis_raw_list)
	{
		if (listOrRow.lstRow)
		 newRow = new omnis_raw_list(listOrRow);
		else if (listOrRow.lstL)
		{
			newRow = new omnis_raw_list(true, listOrRow.__form, listOrRow.__obj);
			newRow.lstDef = listOrRow.lstDef;
			newRow.lstData = listOrRow.lstData[listOrRow.lstL-1];
		}
		else
			console.log("Unable to parse a list as a row when it has no current line");
	}
	else
		console.log("omnis_list_row.ParseRow() input not in a supported format");

	return newRow;
};

// Execute $makelist for a list
// Passed a single argument, which is a function that returns an array using $ref
omnis_raw_list.prototype.$makelist = function(makeArrayFunc)
{
	if (this.lstRow)
		return false;
	var retList = null;
	var lineObj = new omnis_list_row(this, 1);
	for (var i = 1; i <= this.lstData.length; ++i)
	{
		lineObj.mRowNumber = i;
		var row = makeArrayFunc(lineObj);
		
		if (i == 1)
		{
			retList = omnis_raw_list.defineMakelistResult(row);
		}
		retList.lstData.push(row);
		retList.lstLSEL.push(0);
	}
	return retList;
};

// Execute $sendall for a list
// Passed two arguments - the first is a function containing the action to perform for each row, the second (optional)
// determines if the row is included in the rows to be processed.
omnis_raw_list.prototype.$sendall = function(actionFunc, filterFunc)
{
	if (actionFunc == null)
		return false;
	var lineObj = new omnis_list_row(this, 1);
	for (var i = 1; i <= this.lstData.length; ++i)
	{
		lineObj.mRowNumber = i;
		if (filterFunc == null || filterFunc(lineObj))
			actionFunc(lineObj);
	}
	return true;
};

// Return the columns object for the list
omnis_raw_list.prototype.$cols = function()
{
	if (this.__cols)
		return this.__cols;
	this.__cols = new omnis_cols(this);
	return this.__cols;
};

// List $search
// Implementation ported from anumSearch in dmlstn.cpp
omnis_raw_list.prototype.$search = function(compareFunc, fromstart, selonly, dosel, dodesel)
{
	// compareFunc takes $ref as an argument
	// $search(calculation[,bFromStart=kTrue,bOnlySelected=kFalse,bSelectMatches=kTrue,bDeselectNonMatches=kTrue])
	fromstart = jOmnis.defaultParameter(fromstart, true);
	selonly = jOmnis.defaultParameter(selonly, false);
	dosel = jOmnis.defaultParameter(dosel, true);
	dodesel = jOmnis.defaultParameter(dodesel, true);
	
	if (this.lstRow)
		return 0;

	var lineObj = new omnis_list_row(this, 1);
  
  var found = 0; 
	var deselonly = false;
	const whatArr = []; // dmw0613
	if (dosel && !dodesel) deselonly = true; else if (!dosel && dodesel) selonly = true;
	var xx1 = (fromstart ? 1 : (this.lstL + 1));
	var oldCurline = this.lstL;
	if (!(selonly && deselonly) ) for (var xx2 = xx1; xx2 <= this.lstData.length; xx2++)
	{
		if (selonly || deselonly)  
		{
			var ok = this.islinesel(xx2);
			if ((selonly && !ok) || (deselonly && ok)) continue;
		}
		var ok;
		if (compareFunc) 
		{
			lineObj.mRowNumber = xx2;
			this.lstL = xx2;	// Set to allow references like list.col
			ok = (compareFunc(lineObj) != 0);
			this.lstL = oldCurline;
		}
		else ok = true;
    if (dosel || dodesel)  
    {
			if ((ok && dosel) || (!ok && dodesel) && this.islinesel(xx2) != ok) 
			{
				this.selline(xx2, ok);
				whatArr.push("#LSEL"); // dmw0613
				if (!found) found = xx2;
			}
		}
		else if (ok) 
		{ 
			whatArr.push("#L"); // dmw0613
			this.lstL = xx2; found = xx2; break; 
		}
	}
	if (!dosel && ! dodesel && !found) // dmw0613
	{
		this.lstL = found;
		whatArr.push("#L"); // dmw0613
	}

	// Start dmw0613
	if (whatArr.length) {
		const what = whatArr.length === 1 ? whatArr[0] : "";
		this.markAsModified(null, what, found);
	}
	// End dmw0613
	return found;
};

// List $sort
omnis_raw_list.prototype.$sort = function(getValueFunc, descending)
{
	// Note that the arguments occur in pairs - sorting can occur on more than one column
	// First, update the list data to include the line number and selection state as its last two members - this allows us to maintain the selection
	// state of lines after the sort.
	if (this.lstRow || arguments.length == 0)	// rmm7805: Make sure this is a row and there are some sort arguments
		return false;
		
	if (this.lstData.length == 0)
		return true;

	for (var i = 0; i < this.lstData.length; ++i)
	{
		this.lstData[i].push(i + 1, this.islinesel(i + 1) ? 1 : 0);
	}
	// Also save current line indicator - disabled for now, since the core $sort does not do this
	// if (this.lstL && this.lstL >= 1 && this.lstL <= this.lstData.length) 
	// {
	//	var lineData = this.lstData[this.lstL - 1];
	//	lineData[lineData.length - 1] += 2;
	//}
	var saveCurLine = this.lstL;
	
	// Sort the list
	var lineObj = new omnis_list_row(this, 1);
	var sortInfoArray = Array.prototype.slice.call(arguments, 0);
	var sortCols = arguments.length / 2;
	var _this = this;
	
	var newLstData = [].concat(this.lstData);	// Need to keep the original list data as it is until the sort completes, so we can get data from it (the array changes as it is sorted)
	newLstData.sort(function(a, b) 
	{
		var retVal = 0;
		for (var i = 0; i < sortCols; ++i)
		{
			var sortFunc = sortInfoArray[2*i];

			var listLine = a[a.length - 2];
			lineObj.mRowNumber = listLine;
			_this.lstL = listLine;
			var sortVala = sortFunc(lineObj);

			listLine = b[b.length - 2];
			lineObj.mRowNumber = listLine;
			_this.lstL = listLine;
			var sortValb = sortFunc(lineObj);
			
			if (sortInfoArray[2*i + 1])
			{
				// Descending
				var tmp = sortVala;
				sortVala = sortValb;
				sortValb = tmp;
			}
			
			if (typeof sortVala == "number")
				retVal = sortVala - sortValb;
			else if (sortVala instanceof omnis_date && sortValb instanceof omnis_date)
			{
				var jsDatea = sortVala.toJavaScriptDate();
				var jsDateb = sortValb.toJavaScriptDate();
				retVal = jsDatea - jsDateb;
			}
			else if (typeof sortVala == "boolean")
			{
				if (sortVala == sortValb) retVal = 0;
				else if (!sortVala && sortValb) retVal = -1;
				else retVal = 1;
			}
			else
			{
				if (sortVala == sortValb) retVal = 0;
				else if (sortVala < sortValb) retVal = -1;
				else retVal = 1;
			}
			if (retVal != 0)
				break;
		}
		return retVal;
	});
	
	// Replace the list data
	this.lstData = newLstData;
	
	// Update the selection array (and current line) and remove the line number and selection flag from the data
	this.lstLSEL = [];
	this.lstL = saveCurLine;
	for (var i = 0; i < this.lstData.length; ++i)
	{
		var lineData = this.lstData[i];
		var lastIndex = lineData.length - 1;
		this.selline(i + 1, (lineData[lastIndex] & 1) != 0);
		if ((lineData[lastIndex] & 2) != 0)
			this.lstL = i + 1;
		lineData.splice(lastIndex - 1, 2);
	}
	
	// Mark list as modified
	this.markAsModified();
	
	// Done
	return true;
};

// List $totc
omnis_raw_list.prototype.$totc = function(evalFunc, selonly)
{
	// evalFunc takes $ref as an argument and returns the value to be accumulated
	// selonly defaults to kFalse
	var result = 0;
	for (var i = 1; i <= this.lstData.length; ++i)
	{
		var ok = !selonly;
		if (!ok)
		{
			ok = this.islinesel(i);
		}
		if (ok)
		{
			var lineObj = new omnis_list_row(this, i);
			result += evalFunc(lineObj);
		}
	}
	return result;
};

// Row $assigncols
omnis_raw_list.prototype.$assigncols = function()
{
	// Start rmm7844
	var row;
	if (this.lstRow)
		row = this.lstData;
	else
	{
		if (!this.lstL)
			return false;
		row = this.lstData[this.lstL - 1];
		// Start rmm9444
		if (!row)
			this.throwListException("omn_list_badrow");
		// End rmm9444
	}
	// End rmm7844
	// The arguments are the values to assign
	for (var i = 0; i < arguments.length; ++i)
	{
		// rmm7597: Server-side code does not validate this
		// rmm7597: if (i >= this.lstData.length)
		// rmm7597: 	break;
		row[i] = jOmnis.copyValue(arguments[i]);
	}
	this.markAsModified();
	return true;
};

/**
 * Assign the passed row's values to this row. If this is a list, assign to its current line.
 * @param row {omnis_raw_list|omnis_list_row} 	The source row to assign to this row.
 * @param byName {boolean}											If true, assign the cols by name, else by order.
 * @return {boolean}														Success.
 */
omnis_raw_list.prototype.$assignrow = function(row, byName)
{
	// Start jmg0719
	if (!row)
		return false;

	var targetRowData = this.lstRow ? this.lstData : this.lstData[this.lstL-1]; // Get a pointer to the row data (it differs between rows & lists).
	var sourceRow = omnis_raw_list.ParseRow(row); // Normalise the format of the sourceRow
	if (!sourceRow)
		return false;

	// The arguments are the values to assign
	for (var srcCol = 0; srcCol < sourceRow.lstData.length; ++srcCol)
	{
		if (!byName && srcCol >= targetRowData.length)
			break;
		var destCol = byName ? this.resolveColumn(sourceRow.lstDef[srcCol][0]) : srcCol;
		if (destCol != null)
			targetRowData[destCol] = jOmnis.copyValue(sourceRow.lstData[srcCol]);
	}
	// End jmg0719
	this.markAsModified();
	return true;
};

// Row $loadcols (only works for Omnis row variables)
omnis_raw_list.prototype.$loadcols = function(loadcolsfunc,__this)	 // rmm9444: Added __this, passed when called from a remote object
{
  // rmm7745: Now works for lists
	// Call loadcolsfunc() - this will call omnis_form loadcols
	loadcolsfunc(this, __this); // rmm9444
	return true;
};

omnis_raw_list.prototype.insertColumn = function(columnNumber)
{
	columnNumber -= 1;	// Make zero-based
	if (this.lstRow)
	{
		this.lstData.splice(columnNumber, 0, null);
	}
	else
	{
		for (var i = 0; i < this.lstData.length; ++i)
		{
			var line = this.lstData[i];
			line.splice(columnNumber, 0, null);
		}
	}
};

omnis_raw_list.prototype.initColumn = function(columnNumber)
{
	columnNumber -= 1;	// Make zero-based
	var value;
	switch (this.lstDef[columnNumber][1])
	{
		case eOmnisDataTypes.fftCharacter: value = ""; break;
		case eOmnisDataTypes.fftSequence:	// rmm7438
		case eOmnisDataTypes.fftNumber:
		case eOmnisDataTypes.fftInteger: value = 0; break;
		case eOmnisDataTypes.fftBoolean: value = false; break;
		default: value = null;
	}
	if (this.lstRow)
		this.lstData[columnNumber] = value;
	else if (this.actingOnRow) // jmg1210
		this.lstData[this.actingOnRow - 1][columnNumber] = value; // jmg1210
	else
	{
		for (var i = 0; i < this.lstData.length; ++i)
		{
			var line = this.lstData[i];
			line[columnNumber] = value;
		}
	}
	this.markAsModified();
};
// End rmm_jscs2

// Start rmm8482
omnis_raw_list.prototype.total = function(colIndex, selOnly)
{
	var total = 0;
	var data = this.lstData;
	if (this.lstRow)
		return selOnly ? 0 : this.lstData[colIndex];
	for (var i = 0; i < data.length; ++i)
	{
		if (!selOnly || this.islinesel(i + 1))
		{
			var v = data[i][colIndex];
			if (v != null)
				total += v;
		}
	}
	return total;
};

omnis_raw_list.prototype.average = function (colIndex, selOnly)
{
	var total = 0;
	var data = this.lstData;
	if (this.lstRow)
		return selOnly ? 0 : this.lstData[colIndex];
	if (!data.length)
		return 0;
	var count = 0;
	for (var i = 0; i < data.length; ++i)
	{
		if (!selOnly || this.islinesel(i + 1))
		{
			var v = data[i][colIndex];
			if (v != null)
			{
				total += v;
				++count;
			}
		}
	}
	return count ? (total / count) : 0;
};

omnis_raw_list.prototype.minimum = function (colIndex, selOnly)
{
	var min = null;
	var data = this.lstData;
	if (this.lstRow)
		return selOnly ? 0 : this.lstData[colIndex];
	for (var i = 0; i < data.length; ++i)
	{
		if (!selOnly || this.islinesel(i + 1))
		{
			var v = data[i][colIndex];
			if (min == null || (v != null && v < min))
				min = v;
		}
	}
	return min;
};

omnis_raw_list.prototype.maximum = function (colIndex, selOnly)
{
	var max = null;
	var data = this.lstData;
	if (this.lstRow)
		return selOnly ? 0 : this.lstData[colIndex];
	for (var i = 0; i < data.length; ++i)
	{
		if (!selOnly || this.islinesel(i + 1))
		{
			var v = data[i][colIndex];
			if (max == null || (v != null && v > max))
				max = v;
		}
	}
	return max;
};
// End rmm8482

/**
 * Gets an omnis_raw_list containing a subset of this list's data.
 * @param {Number} startIndex Index that the subset should start from (inclusive).
 * @param {Number} [endIndex] Index the subset should end at (inclusive). If omitted, it will return up to the end of the source list.
 * @returns {omnis_raw_list}
 */ // jmg0251
omnis_raw_list.prototype.getSubset = function (startIndex, endIndex)
{
	var subset = new omnis_raw_list(this, this.__form, this.__obj); // rmm9444
	subset.lstData = [];
	subset.lstLSEL = []; // jmg0679

	if (endIndex == null || endIndex > this.lstData.length - 1) // jmg0424
		endIndex = this.lstData.length - 1; // Run up to the end of the source list.

	for (var i = startIndex; i <= endIndex; i++) {
		subset.lstData.push(this.lstData[i]);
		subset.lstLSEL.push(this.lstLSEL[i]); // jmg0679
	}

	return subset;
};

/**
	* rmm9444: Throws an exception from an omnis_raw_list method
	* @param {String} exceptionName The name of the exception, used to look up the localized string describing it
*/
omnis_raw_list.prototype.throwListException = function(exceptionName)
{
	var inst;
	if (this.__obj && this.__obj.__form)
		inst = this.__obj.__form;
	else if (this.__form)
		inst = this.__form.inst;
	else
		inst = jOmnis.getOmnisInst(0);
	throw jGetOmnisException(inst, exceptionName); 
}

// Start rmm7746: process any number of parameters for list notation, to allow access to data arbitrarily deep
// pAssignValue is passed when assigning a value
function jListNotation(pObj, pArgs, pArgCount, pAssignValue)
{
	if (pArgCount == 0)
		return null;
	var assign = (arguments.length == 4);
	var curValue = pObj;
	var listRow;
	for (var i = 0; i < pArgCount; ++i)
	{
		if (curValue == null)
			break;
		var arg = pArgs[i];
		if (curValue instanceof omnis_raw_list)
		{
			if (curValue.lstRow)
			{
				var colIndex = curValue.resolveColumn(arg);
				if (colIndex == null)
					return null;
				if (assign && i == (pArgCount - 1))
				{
					pAssignValue = curValue.convertDataType(pAssignValue, colIndex);
					curValue.lstData[colIndex] = pAssignValue;
					curValue = true;	// Mark containing list as modified
				}
				else
				{
					curValue = curValue.lstData[colIndex];
				}
			}
			else
			{
				if (jOmnis.isInteger(arg))
				{
					// Must be a row
					var lineIndex = arg * 1 - 1;
					if (lineIndex < 0 || lineIndex >= curValue.lstData.length)
						return null;

					if (!listRow)
						listRow = new omnis_list_row();
					listRow.mList = curValue;
					listRow.mRowNumber = arg * 1;

					curValue = listRow;
					if (assign && i == (pArgCount - 1))
					{
						// Need to assign the assign the value to the row
						curValue = curValue.assign(pAssignValue);
					}
				}
				else
				{
					// Must be a column in the current line
					if (curValue.lstL < 1 || curValue.lstL > curValue.lstData.length)
						return null;
					var colIndex = curValue.resolveColumn(arg);
					if (colIndex == null)
						return null;
					if (assign && i == (pArgCount - 1))
					{
						pAssignValue = curValue.convertDataType(pAssignValue, colIndex);
						curValue.lstData[curValue.lstL - 1][colIndex] = pAssignValue;
						curValue = true;	// Mark containing list as modified
					}
					else
					{
						curValue = curValue.lstData[curValue.lstL - 1][colIndex];
					}
				}
			}
		}
		else if (curValue instanceof omnis_list_row)
		{
			// Must be a column in the row
			var colIndex = curValue.mList.resolveColumn(arg);
			if (colIndex == null)
				return null;
			if (curValue.mRowNumber >= 1 && curValue.mRowNumber <= curValue.mList.lstData.length)
			{
				if (assign && i == (pArgCount - 1))
				{
					pAssignValue = curValue.mList.convertDataType(pAssignValue, colIndex);
					curValue.mList.lstData[curValue.mRowNumber - 1][colIndex] = pAssignValue;
					// Start rmm11584: If we are setting a column in a top-level list, then we can record just the single updated column
					if (!i && 1 == pArgCount && ("__dataIndex" in curValue.mList))
						curValue.mSingleColumn = colIndex + 1;
					// End rmm11584
					curValue = true;	// Mark containing list as modified
				}
				else
				{
					curValue = curValue.mList.lstData[curValue.mRowNumber - 1][colIndex];
				}
			}
			else
				return null;
		}
		else
		{
			return null;
		}
	}
	return curValue;
}
